<template>
  <div class="container">
    <h1>Баланс на {{ currentMonth }}</h1>
    <section v-if="error">
      <p>{{error}}</p>
    </section>
    <section v-else>
      <div v-if="loading">
        <b-spinner label="Spinning"/>
      </div>
      <div v-else>
        <b-card-group deck>
          <b-card border-variant="info" header="Баланс на косметику" align="center">
            <b-card-text>
              <h2>{{ cosmeticsBalance }}</h2>
            </b-card-text>
          </b-card>
          <b-card border-variant="info" header="Баланс на шмотки" align="center">
            <b-card-text>
              <h2>{{ clothesBalance }}</h2>
            </b-card-text>
          </b-card>
          <b-card border-variant="info" header="Общий баланс" align="center">
            <b-card-text>
              <h2>{{ balance }}</h2>
            </b-card-text>
          </b-card>
        </b-card-group>
      </div>
    </section>
    <br>
    <h1>Последние покупки</h1>
    <hr>
    <div>
      <b-card bg-variant="light">
        <b-form-group
          label-cols-lg="3"
          label="Добавить покупку"
          label-size="lg"
          label-class="font-weight-bold pt-0"
          class="mb-0"
        >
          <b-form-group
            label-cols-sm="3"
            label="Сума (грн):"
            label-align-sm="right"
            label-for="purchAmount"
          >
            <b-form-input id="purchAmount" v-model="amount"/>
          </b-form-group>

          <b-form-group
            label-cols-sm="3"
            label="Описание:"
            label-align-sm="right"
            label-for="purchText"
          >
            <b-form-input id="purchText" v-model="text"/>
          </b-form-group>
          <b-form-group
            label-cols-sm="3"
            label="Дата покупки:"
            label-align-sm="right"
            label-for="purchDate"
          >
            <b-form-input id="purchDate" v-model="purchDate" type="date"/>
          </b-form-group>

          <b-form-group label="Тип покупки">
            <b-form-radio-group
              v-model="selectedType"
              :options="typeOptions"
              name="purch-type-options"
            />
          </b-form-group>
          <b-button variant="success" v-on:click="createPurch">Добавить!</b-button>
        </b-form-group>
      </b-card>
    </div>
    <hr>
    <b-alert show variant="danger" v-if="error">{{ error }}</b-alert>
    <div v-else-if="loading">
      <b-spinner label="Spinning"/>
    </div>
    <div v-else>
      <b-list-group>
        <b-list-group-item
          href="#"
          class="d-flex justify-content-between align-items-center"
          v-for="(purch, index) in purchases"
          v-bind:item="purch"
          v-bind:index="index"
          v-bind:key="purch._id"
        >
          <h5
            class="text-muted"
          >{{ `${purch.createdAt.getDate()}/${purch.createdAt.getMonth()+1}/${purch.createdAt.getFullYear()}` }}</h5>
          <div class="text-center">
            <h4>
              <b-badge variant="secondary">{{ purch.purchType }}</b-badge>
              {{ purch.text }}
              <b-badge variant="info" pill>{{ purch.amount }}</b-badge>
            </h4>
          </div>
          <b-button size="sm" variant="danger" v-on:click="deletePurch(purch._id)">Удалить</b-button>
        </b-list-group-item>
      </b-list-group>
    </div>
  </div>
</template>

<script>
import PurchService from "../services/PurchService";

export default {
  name: "Purchases",
  data() {
    return {
      currentDate: new Date(),
      balance: 0,
      cosmeticsBalance: 0,
      clothesBalance: 0,
      loading: true,
      error: "",
      purchases: [],
      typeOptions: [
        { text: "Косметика", value: "cosmetics" },
        { text: "Одежда", value: "clothes" }
      ],
      selectedType: "cosmetics",
      text: "",
      amount: "",
      purchDate: PurchService.convert(new Date())
    };
  },
  computed: {
    currentMonth: function() {
      var months = [
        "Январь",
        "Февраль",
        "Март",
        "Апрель",
        "Май",
        "Июнь",
        "Июль",
        "Август",
        "Сентябрь",
        "Октябрь",
        "Ноябрь",
        "Январь"
      ];
      var thisMonth = months[this.currentDate.getMonth()];

      return thisMonth.toLocaleString("ru-ru", { month: "long" });
    }
  },
  methods: {
    async getBalance() {
      try {
        this.balance = await PurchService.getBalance(this.currentDate);
        this.cosmeticsBalance = await PurchService.getBalanceByType(
          this.currentDate,
          "cosmetics"
        );
        this.clothesBalance = await PurchService.getBalanceByType(
          this.currentDate,
          "clothes"
        );
        this.loading = false;
      } catch (err) {
        this.error = err.message;
      }
    },
    async getPurchases() {
      try {
        this.purchases = await PurchService.getPurchases();
        this.loading = false;
      } catch (err) {
        this.error = err.message;
      }
    },
    clearСreatePurchVars() {
      this.text = "";
      this.amount = "";
      this.purchDate = PurchService.convert(new Date());
    },
    async createPurch() {
      try {
        await PurchService.createPurchase(
          this.text,
          this.amount,
          this.selectedType,
          this.purchDate
        );
        await this.getPurchases();
        await this.getBalance();
        this.clearСreatePurchVars();
      } catch (err) {
        this.error = err.message;
      }
    },
    async deletePurch(id) {
      try {
        await PurchService.deletePurchase(id);
        await this.getPurchases();
      } catch (err) {
        this.error = err.message;
      }
    }
  },
  async mounted() {
    await this.getPurchases();
    await this.getBalance();
  },
  async created() {
    await this.getPurchases();
    await this.getBalance();
  }
};
</script>
