import axios from "axios";

const url = "api/purchases/";

class PurchService {
  static convert(dateStr) {
    let date = new Date(dateStr),
      mnth = ("0" + (date.getMonth() + 1)).slice(-2),
      day = ("0" + date.getDate()).slice(-2);
    return [date.getFullYear(), mnth, day].join("-");
  }
  static getFirstMthDate(date) {
    let newDate = date.toISOString().slice(0, 8);
    newDate += "01";
    return newDate;
  }
  static getBalanceByType(date, type) {
    return new Promise(async (resolve, reject) => {
      try {
        const res = await PurchService.getPurchasesByDate(PurchService.getFirstMthDate(date));
        let sum = 1500;
        for (let counter = 0; counter < res.length; counter++) {
          let currentBalance = res[counter];
          if (currentBalance.type == type) {
            sum -= currentBalance.amount;
          }
        }
        resolve(sum);
      } catch (err) {
        reject(err);
      }
    });
  }

  static getBalance(date) {
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.get(
          `${url}balance/${date.toISOString().slice(0, 10)}`
        );
        const data = res.data;
        resolve(data.balance);
      } catch (err) {
        reject(err);
      }
    });
  }

  //Get Purchases
  static getPurchases() {
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.get(url);
        const data = res.data;
        resolve(
          data.map(purchase => ({
            ...purchase,
            createdAt: new Date(purchase.createdAt)
          }))
        );
      } catch (err) {
        reject(err);
      }
    });
  }

  static getPurchasesByDate(date) {
    return new Promise(async (resolve, reject) => {
      try {
        const res = await PurchService.getPurchases();
        let result = [];
        let filterDate = new Date(date);
        if (res && filterDate) {
          for (let counter = 0; counter < res.length; counter++) {
            let current = res[counter];
            if (current.createdAt >= filterDate) {
              result.push(current);
            }
          }
        }
        resolve(result);
      } catch (err) {
        reject(err);
      }
    });
  }

  //Create Purchases
  static createPurchase(text, amount, type, purchDate) {
    return axios.post(url, {
      text,
      amount,
      type,
      purchDate
    });
  }

  //Delete Purchases
  static deletePurchase(id) {
    return axios.delete(`${url}${id}`);
  }
}

export default PurchService;